@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 10px;
}

div.app {
  position: relative;
  min-height: 120vh;
  max-width: 1650px;
  margin: 0 auto;
}
div.menuHead {
    display: flex;
    justify-content: space-around;
    margin: 5px 3%;
}

div.menuHead a {
    display: inline-block;
    font-size: 1.2rem;
    color: black;
    text-decoration: none;
    padding: 5px 10px;
    border-radius: 5px;
}

div.menuHead a:hover {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

div.menuHead i {
    margin-right: 3px;
}

@media (max-width:500px) and (orientation:portrait) {
    div.menuHead {
        flex-direction: column;
    }
}



div.menuHead img {
    display: block;
    width: 20%;
    margin: 2% auto;
}

@media (min-width:501px) {
    div.menuHead img.hamburger {
        display: none;
    }
}

@media (max-width:500px) {
    div.menuHead a {
        display: none;
    }


    div.menuHead:hover a {
        display: inline-block;
        /* color: red; */
    }
}
div.header {
    position: relative;
    height: 60vh;

    /* background-image: url("../img/sstable.jpg"); */
    background-image: url(/01-2021/static/media/istockphoto-1136638047-1024x1024.038584ca.jpg);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center 50%;
    transition: 1s;
}

div.header img {
    position: absolute;
    top: 10%;
    left: 10%;
    max-width: 20%;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    border-radius: 10px;
}

div.header:hover {
    background-size: 105%;
}

@media (max-width:500px) {
    div.header {
        height: 30vh;
    }
}
div.mainMenu ul {
    list-style: none;
    margin: 5px 5% 20px;
    height: 80px;
    text-align: center;
}

div.mainMenu ul li {
    display: inline-block;
    padding: 5px 3%;
    margin: 2px 2px;
    border: 2px solid white;
    background-color: white;
    transition: .3s;
}

div.mainMenu ul li a {
    font-size: 1.7rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-decoration: none;
    color: black;
}

div.mainMenu ul li a:hover {
    color: rgb(75, 73, 73);
}

div.mainMenu ul li:hover {
    border: 2px solid gray;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
}
div.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: rgb(128, 128, 128);
    padding: 2% 0;
    margin-top: 10%;
}

div.footer p {
    font-size: 1.2rem;
    text-align: center;
}
div.home {
    text-align: center;
}

div.home img {
    width: 10%;
}

div.home p {
    font-size: 1.5rem;
}
div.products {
